.cashCard {
    border: 1px transparent;
    border-radius: 30px;
    background-color: transparent;
}

.cashCard .header {
    border-radius: 30px 30px 0px 0px;
    border: 1px solid;
    border-color: var(--main-card-color);
    font-size: 60px;
    background-color: var(--main-card-color)
}

.cashCard .header .currencyContainer > .currency {
    font-size: 30px;
    border:transparent;
    background-color: var(--main-card-color);
    color:white;
    margin:0;
    padding: 0;
    width: 30px;
}

.cashCard .header .currencyContainer {
    font-size: 30px;
    border: white 1px solid;
    border-radius: 50%;
    height: 2.7em;
    width: 2.7em;
    margin: .3em 0em;
}


.cashCard .body {
    border-left: 1px solid;
    border-right: 1px solid;
    border-bottom: 1px solid;
    border-color: var(--main-card-color);
    background-color: white;
}

.cashCard .body .title {
    color: dimgray;
    text-align: start;
    font-size: 20px;
}

.cashCard .body .subTitle {
    color: black;
    text-align: start;
    font-size: 17px;
}

.lighter {
    font-weight: lighter
}

.cashCard .body .balanceAmount {
    padding: 0;
    text-align: end;
    font-size: 20px;
    color: dimgray
}

.cashCard .body .balance {
    padding: 0;
    float: right;
    font-size: 17px;
}

.bolder {
    font-weight: bolder
}

.lighter {
    font-weight: lighter
}

.cashCard .footer {
    background-color: transparent;
    border: none;
    border-radius: 0px 0px 30px 30px;
    border-color: var(--main-card-color)
}

.cashCard .footer .button {
    height: 2em;
    font-size: 30px;
    background-color: white;
    border: 1px solid;
    border-color: var(--main-card-color);
    color: var(--main-card-color);
    width: 100%;
}
.cashCard .footer .button .label{
    font-size: 20px;
    
}

.cashCard .footer  .left{
    border-radius: 0px 0px 0px 30px;

}

.cashCard .footer .right{
    border-radius: 0px 0px 30px 0px;
}
